
body{
    background-color:#0820FF;
    display:flex;
    align-items:center;
    justify-content: center;
    min-height:100vh;
    color: white;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .flex {
    display:flex;
    align-items:center;
    justify-content:center;
    color:white;
  }
  
  .timer-container {
    margin:20px;
  }
  
  .timer-container h1{
    text-align:center;
  }
  
  button {
    cursor:pointer;
    background-color: transparent;
    padding: 15px;
    font-size:20px;
    margin: 15px;
    color: #fff;
  }
  
  .actions-wrapper span {
    font-size:40px;
    margin: 10px;
  }
  
  .clock-container{
    border: 2px solid #fff;
    border-radius:20px;
    text-align:center;
    
  }
  
  .clock-container span{
    font-size:50px;

  }